      <template #left>
        <div id="sub-title">
          <i class="iconfont icon-dianji" />
        </div>
      </template>
    </Tabinfor>
    <el-divider /><!---->
    <!DOCTYPE html>
    <div>
      <label>Select DICOM file series or DICOM file:</label>
      <input name="inputFile" type="file" ref="refFileA" multiple="multiple" @change="outputFileInformation($event)">
      <!-- <input class="sliceI" type="range" min="0" max="63" step="1" value="30"> -->
    </div>
    <!-- File information -->
    <textarea readonly name="fileInformation">File information...</textarea>
    <div id="container"></div>
  </div>
</template>


<script>
import {
  createSrc,
