// import controlPanel from './controlPanel.html';

import vtkColorMaps from 'vtk.js/Sources/Rendering/Core/ColorTransferFunction/ColorMaps';
export default {
  name: "Detectchanges",
  components: {
    Tabinfor,
    Bottominfor,
    DraggableItem
  },

  beforeRouteEnter(to, from, next) {
    next((vm) => {
      document.querySelector(".el-main").scrollTop = 0;
    });
  },
  data() {
    return {
      holeShow: true,
      isSliderLocked: false,
      preMode: 1,
      pairs: [],
      hisPairs: [],
      shaPairs: [],
      canUpload: true,
      onRender: 0,
      isUpload: true,
      hisNum: 0,
      value: null,
      renderstyle: "",
      funtype: "",
      scrollTop: "",

      currentQroup: 0,
      currentIndex: 0,

      fileList1: [],
      fileList2: [],

      uploadSrc1: [],
      uploadSrc2: [],
      uploadSrc: [],
      upload: {
        window_size: 256,
        stride: 128,
        list: [
          {
            first: "",
            second: "",
          },
        ],
        prehandle: 0,
        denoise: 0,
        model_path: ''
      },
      modelPathArr: [],
    };
  },
  created() {
    this.getMore();
    this.getCustomModel('change_detection').then((res) => {
      this.modelPathArr = res.data.data
      this.upload.model_path = this.modelPathArr[0]?.model_path
    }).catch((rej) => { })
  },

  methods: {
    downloadimgWithWords,
    historyGetPage,
    imgUpload,
    getCustomModel,
    createSrc,
    getImgArrayBuffer,
    atchDownload,
    histogramUpload,
    outputFileInformation(event) {

      var outputTextArea = document.querySelector('textarea')
      outputTextArea.textContent = "Loading...";
      const dataTransfer = event.dataTransfer
      const files = event.target.files || dataTransfer.files

      function printImage(image) {
        function replacer(key, value) {
          if (!!value && value.byteLength !== undefined) {
            return String(value.slice(0, 6)) + '...'
          }
          return value
        }
        outputTextArea.textContent = JSON.stringify(image, replacer, 4)
      }
      printImage(files.length)
      if (files.length === 1) {
        return readImageFile(null, files[0])
          .then(function ({
            image,
            webWorker
          }) {
            webWorker.terminate()
            printImage(image) //

            const imageData = vtkITKHelper.convertItkToVtkImage(image);
            // printImage(imageData)
            const source = imageData;
            const view3d = document.querySelector('#container');
            const fullScreenRenderer = vtkFullScreenRenderWindow.newInstance({
              rootContainer: view3d,
              containerStyle: {
                height: '100%',
                overflow: 'hidden',
              },
              background: [0, 0, 0],
            });
            const renderer = fullScreenRenderer.getRenderer();
            const renderWindow = fullScreenRenderer.getRenderWindow();
            // renderWindow.getInteractor().setDesiredUpdateRate(15);
            const actor = vtkVolume.newInstance();
            const mapper = vtkVolumeMapper.newInstance();

            mapper.setInputData(source);
            actor.setMapper(mapper);
            // mapper.setSampleDistance(0.7);
            const sampleDistance =
              0.7 *
              Math.sqrt(
                source
                  .getSpacing()
                  .map((v) => v * v)
                  .reduce((a, b) => a + b, 0),
              );
            mapper.setSampleDistance(sampleDistance);
            const activeCamera = renderer.getActiveCamera();
            activeCamera.setPosition(0, -1, 0);
            activeCamera.setFocalPoint(0, 0, 0);
            activeCamera.setViewUp(0, 0, 1);
            renderer.addActor(actor);
            renderer.resetCamera();
            renderWindow.render();
          })
      } else {
        return readImageDICOMFileSeries(null, files).then(({
          webWorker,
          image
        }) => {
          webWorker.terminate()
          printImage("source")
          const imageData = vtkITKHelper.convertItkToVtkImage(image);
          printImage(imageData)
          const source = imageData;
          const view3d = document.querySelector('#container');
          const fullScreenRenderer = vtkFullScreenRenderWindow.newInstance({
            rootContainer: view3d,
            containerStyle: {
              height: '100%',
              overflow: 'hidden',
            },
            background: [0, 0, 0],
          });
          const renderer = fullScreenRenderer.getRenderer();
          const renderWindow = fullScreenRenderer.getRenderWindow();
          renderWindow.getInteractor().setDesiredUpdateRate(15);
          const actor = vtkVolume.newInstance();
          const mapper = vtkVolumeMapper.newInstance();

          mapper.setInputData(source);
          actor.setMapper(mapper);
          // mapper.setSampleDistance(0.7);
          const sampleDistance =
            0.7 *
            Math.sqrt(
              source
                .getSpacing()
                .map((v) => v * v)
                .reduce((a, b) => a + b, 0),
            );
          mapper.setSampleDistance(sampleDistance);
          const activeCamera = renderer.getActiveCamera();
          activeCamera.setPosition(0, -1, 0);
          activeCamera.setFocalPoint(0, 0, 0);
          activeCamera.setViewUp(0, 0, 1);
          const lookupTable = vtkColorTransferFunction.newInstance();
          const piecewiseFun = vtkPiecewiseFunction.newInstance();

          // set up color transfer function
          lookupTable.applyColorMap(vtkColorMaps.getPresetByName('Cool to Warm'));//
          // hardcode an initial mapping range here.
          // Normally you would instead use the range from
          // imageData.getPointData().getScalars().getRange()
          lookupTable.setMappingRange(0, 256);
          lookupTable.updateRange();

          // set up simple linear opacity function
          // This assumes a data range of 0 -> 256
          for (let i = 0; i <= 8; i++) {
            piecewiseFun.addPoint(i * 32, i / 8);
          }

          // set the actor properties
          actor.getProperty().setRGBTransferFunction(0, lookupTable);
          actor.getProperty().setScalarOpacity(0, piecewiseFun);
          renderer.addActor(actor);
          renderer.resetCamera();
          renderWindow.render();



          // const { SlicingMode } = ImageConstants;
          // const container1 = document.querySelector('#container');
          // const genericRenderWindow1 = vtkGenericRenderWindow.newInstance();
          // genericRenderWindow1.setContainer(container1);
          // genericRenderWindow1.resize();
          // const renderer1 = genericRenderWindow1.getRenderer();
          // const renderWindow1 = genericRenderWindow1.getRenderWindow();
          // renderer1.getActiveCamera().setParallelProjection(true);
          // // --- Set up interactor style for image slicing
          // const istyle = vtkInteractorStyleImage.newInstance();
          // istyle.setInteractionMode('IMAGE_SLICING');
          // renderWindow1.getInteractor().setInteractorStyle(istyle);
          // const actor1 = vtkImageSlice.newInstance();
          // const mapper1 = vtkImageMapper.newInstance();
          // mapper1.setSliceAtFocalPoint(true);
          // mapper1.setInputData(source);
          // mapper1.setSlicingMode(SlicingMode.K);
          // actor1.setMapper(mapper1);
          // actor1.getProperty().setColorWindow(255);
          // actor1.getProperty().setColorLevel(127);
          // renderer1.addActor(actor1);
          // renderer1.resetCamera();
          // renderWindow1.render();

          // const imageActorI = vtkImageSlice.newInstance();
          // const imageActorJ = vtkImageSlice.newInstance();
          // const imageActorK = vtkImageSlice.newInstance();

          // renderer.addActor(imageActorK);
          // renderer.addActor(imageActorJ);
          // renderer.addActor(imageActorI);

          // function updateColorLevel(e) {
          //   const colorLevel = Number(
          //     (e ? e.target : document.querySelector('.colorLevel')).value
          //   );
          //   imageActorI.getProperty().setColorLevel(colorLevel);
          //   imageActorJ.getProperty().setColorLevel(colorLevel);
          //   imageActorK.getProperty().setColorLevel(colorLevel);
          //   renderWindow.render();
          // }

          // function updateColorWindow(e) {
          //   const colorLevel = Number(
          //     (e ? e.target : document.querySelector('.colorWindow')).value
          //   );
          //   imageActorI.getProperty().setColorWindow(colorLevel);
          //   imageActorJ.getProperty().setColorWindow(colorLevel);
          //   imageActorK.getProperty().setColorWindow(colorLevel);
          //   renderWindow.render();
          // }
          //     const data = imageData
          //     const dataRange = data.getPointData().getScalars().getRange();
          //     const extent = data.getExtent();

          //     const imageMapperK = vtkImageMapper.newInstance();
          //     imageMapperK.setInputData(data);
          //     imageMapperK.setKSlice(1);
          //     imageActorK.setMapper(imageMapperK);

          //     const imageMapperJ = vtkImageMapper.newInstance();
          //     imageMapperJ.setInputData(data);
          //     imageMapperJ.setJSlice(50);
          //     imageActorJ.setMapper(imageMapperJ);

          //     const imageMapperI = vtkImageMapper.newInstance();
          //     imageMapperI.setInputData(data);
          //     imageMapperI.setISlice(50);
          //     imageActorI.setMapper(imageMapperI);

          //     renderer.resetCamera();
          //     renderer.resetCameraClippingRange();
          //     renderWindow.render();

          //     ['.sliceI', '.sliceJ', '.sliceK'].forEach((selector, idx) => {
          //       const el = document.querySelector(selector);
          //       el.setAttribute('min', extent[idx * 2 + 0]);
          //       el.setAttribute('max', extent[idx * 2 + 1]);
          //       el.setAttribute('value', 30);
          //     });

          //     ['.colorLevel', '.colorWindow'].forEach((selector) => {
          //       document.querySelector(selector).setAttribute('max', dataRange[1]);
          //       document.querySelector(selector).setAttribute('value', dataRange[1]);
          //     });
          //     document
          //       .querySelector('.colorLevel')
          //       .setAttribute('value', (dataRange[0] + dataRange[1]) / 2);
          //     updateColorLevel();
          //     updateColorWindow();
            

          // document.querySelector('.sliceI').addEventListener('input', (e) => {
          //   imageActorI.getMapper().setISlice(Number(e.target.value));
          //   renderWindow.render();
          // });

          // document.querySelector('.sliceJ').addEventListener('input', (e) => {
          //   imageActorJ.getMapper().setJSlice(Number(e.target.value));
          //   renderWindow.render();
          // });

          // document.querySelector('.sliceK').addEventListener('input', (e) => {
          //   imageActorK.getMapper().setKSlice(Number(e.target.value));
          //   renderWindow.render();
          // });

          // document
          //   .querySelector('.colorLevel')
          //   .addEventListener('input', updateColorLevel);
          // document
          //   .querySelector('.colorWindow')
          //   .addEventListener('input', updateColorWindow);
        })
      }
    },

    clearQueue() {
      this.fileList1 = [];
      this.fileList2 = [];
      this.$message.success("");
    },
    goRenderThese(index) {
      this.currentQroup = 5 * index;
      this.currentIndex = 5 * index;
      this.goRenderThis(0);
    },
    uploadfile() {
      this.uploadSrc = [];
      if (this.upload.window_size === this.upload.stride) {
        this.$message.error('')
        return
      }
      if (
        this.fileList1.length !== this.fileList2.length ||
        this.fileList1.length === 0
      ) {
        this.$message.error("");
      } else {
        let formData1 = new FormData();
        let formData2 = new FormData();
        for (const item of this.fileList1) {
          formData1.append("files", item) ||
            formData1.append("files", item.raw);
          formData1.append("type", "");
        }
        for (const item of this.fileList2) {
          formData2.append("files", item) ||
            formData2.append("files", item.raw);
          formData2.append("type", "");
        }
        let upload1 = new Promise((resolve, reject) => {
          this.createSrc(formData1).then((res) => {
            this.uploadSrc1 = res.data.data;
            resolve();
          }).catch((rej) => { })
        });
        let upload2 = new Promise((resolve, reject) => {
          this.createSrc(formData2).then((res) => {
            this.uploadSrc2 = res.data.data;
            resolve();
          }).catch((rej) => { })
        });
        Promise.all([upload1, upload2])
          .then((val) => {
            this.uploadSrc = this.uploadSrc1.concat(this.uploadSrc2);
            this.uploadSrc = this.uploadSrc.map((item) => {
              return {
                filename: item.filename.substring(
                  item.filename.indexOf("/") + 1,
                  item.length
                ),
                src: item.src,
              };
            });

            this.pairs = this.uploadSrc.map((item) => {
              return item.filename.substring(
                item.filename.indexOf("/") + 1,
                item.length
              );
            });

            this.checkPairs(this.pairs);
            if (!this.canUpload) {
              this.$message.error(
                ""
              );
            } else {
              this.upload.list = this.getList(this.uploadSrc);
              this.imgUpload(this.upload, 'change_detection')
                .then((res) => {
                  this.$refs.uploadA.clearFiles();
                  this.$refs.uploadB.clearFiles();
                  this.fileList1 = [];
                  this.fileList2 = [];
                  this.$message.success("");
                  this.isUpload = true;
                  this.getMore();
                  if (this.upload.list.length >= 10) {
                    this.$confirm(
                      "?",
                      "",
                      {
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning",
                      }
                    )
                      .then(() => {
                        this.goCompress(this.upload.list.length);
                      })
                      .catch(() => { });
                  }
                })
                .catch((rej) => { });
            }
          })
          .catch((rej) => { });
      }
    },
    getList(beforeData) {
      //
      //https://blog.csdn.net/weixin_45575273/article/details/108321137?utm_medium=distribute.pc_aggpage_search_result.none-task-blog-2~aggregatepage~first_rank_ecpm_v1~rank_v31_ecpm-3-108321137-null-null.pc_agg_new_rank&utm_term=%E6%95%B0%E7%BB%84%E4%B8%AD%E7%9A%84%E5%AF%B9%E8%B1%A1id%E7%9B%B8%E5%90%8C%E7%9A%84%E5%AF%B9%E8%B1%A1%E7%BB%84%E6%88%90%E4%B8%80%E4%B8%AA%E6%96%B0%E6%95%B0%E7%BB%84&spm=1000.2123.3001.4430
      let tempArr = [];
      let afterData = [];
      for (let i = 0; i < beforeData.length; i++) {
        if (tempArr.indexOf(beforeData[i].filename) === -1) {
          afterData.push(beforeData[i]);
          tempArr.push(beforeData[i].filename);
        } else {
          for (let j = 0; j < afterData.length; j++) {
            Reflect.deleteProperty(afterData[j], "photo_id");
            // Reflect.deleteProperty(afterData[j], "filename");
            afterData[j].first = afterData[j].src;
            afterData[j].second = beforeData[i + j].src;
            // Reflect.deleteProperty(afterData[j], "src");
          }
          break;
        }
      }
      return afterData;
    },
    goCompress(num) {
      this.$message.success("");
      this.historyGetPage(1, num, "").then((res) => {
        this.atchDownload(
          res.data.data.map((item) => {
            return { after_img: item.after_img, id: item.id };
          })
        );
      }).catch((rej) => { })
    },
    getMore() {
      this.historyGetPage(1, 20, "")
        .then((res) => {
          res.data.data.forEach((item) => {
            item['before_img1'] = global.BASEURL + item.before_img1
            item['before_img'] = global.BASEURL + item.before_img
            item['after_img'] = global.BASEURL + item.after_img
            item.data['hole'] = global.BASEURL + item.data['hole']
            item.data['hole_style'][0] = global.BASEURL + item.data['hole_style'][0]
            item.data['hole_style'][1] = global.BASEURL + item.data['hole_style'][1]
            item.data['hole_style'][2] = global.BASEURL + item.data['hole_style'][2]
            item.data['hole_style'][3] = global.BASEURL + item.data['hole_style'][3]
            item.data[0] = global.BASEURL + item.data[0]
            item.data[1] = global.BASEURL + item.data[1]
            item.data[2] = global.BASEURL + item.data[2]
            item.data[3] = global.BASEURL + item.data[3]
            item.data['mask'] = global.BASEURL + item.data.mask
            item.data['mask_hole'] = global.BASEURL + item.data.mask_hole
          })
          this.resultArr = res.data.data
          this.onRenderResult = this.resultArr[this.currentIndex].after_img
        })
        .catch((rej) => { });
    },
    selectHistogram() {
      if (this.$refs.histogram.checked === true) {
        if (
          this.fileList1.length !== this.fileList2.length ||
          this.fileList1.length === 0
        ) {
          if (this.upload.prehandle === 1) {
            this.$refs.histogram.checked = false;
            this.upload.prehandle = 0;
          } else {
            this.$refs.histogram.checked = false;
            this.$message.error("");
          }
        } else {
          this.upload.prehandle = 1;
          this.myhistogram.prehandle = 1;
          this.$message.success("");
          if (this.$refs.sharpen.checked === true) {
            this.$refs.sharpen.checked = false;
          }
          let formData1 = new FormData();
          let formData2 = new FormData();

          for (const item of this.fileList1) {
            formData1.append("files", item) ||
              formData1.append("files", item.raw);
            formData1.append("type", "");
          }

          for (const item of this.fileList2) {
            formData2.append("files", item) ||
              formData2.append("files", item.raw);
            formData2.append("type", "");
          }
          let upload3 = new Promise((resolve, reject) => {
            this.createSrc(formData1).then((res) => {
              this.uploadSrc3 = res.data.data.splice(0, 3);
              this.Img1 = this.uploadSrc3.map((item) => {
                return global.BASEURL + item.src;
              });
              resolve();
            }).catch((rej) => { })
          });
          let upload4 = new Promise((resolve, reject) => {
            this.createSrc(formData2).then((res) => {
              this.uploadSrc4 = res.data.data.splice(0, 3);
              this.Img3 = this.uploadSrc4.map((item) => {
                return global.BASEURL + item.src;
              });
              resolve();
            }).catch((rej) => { })
          });
          Promise.all([upload3, upload4]).then((val) => {
            this.histogramSrc = this.uploadSrc3.concat(this.uploadSrc4);
            //https://blog.csdn.net/qq_27342239/article/details/118078113/
            this.histogramSrc = this.histogramSrc.map((item) => {
              return {
                filename: item.filename.substring(
                  item.filename.indexOf("/") + 1,
                  item.length
                ),
                src: item.src,
              };
            });

            this.hisPairs = this.histogramSrc.map((item) => {
              return item.filename.substring(
                item.filename.indexOf("/") + 1,
                item.length
              );
            });

            this.checkPairs(this.hisPairs);

            if (!this.canUpload) {
              this.$message.error(
                ""
              );
              this.Img1 = [];
              this.Img3 = [];
            } else {
              this.myhistogram.list = this.getList(this.histogramSrc);
              this.histogramUpload(this.myhistogram).then((res) => {
                this.Img2 = res.data.data.map((item) => {
                  return global.BASEURL + item;
                });
                this.Img2 = this.Img2.splice(0, 3);
              }).catch((rej) => { })
            }
          }).catch((rej) => { })
        }
      } else {
        this.$message.success("");
        this.upload.prehandle = 0;
        this.myhistogram.prehandle = 0;
      }
    },
    selectSharpen() {
      if (
        this.fileList1.length !== this.fileList2.length ||
        this.fileList1.length === 0
      ) {
        if (this.upload.prehandle === 4) {
          this.$refs.sharpen.checked = false;
          this.upload.prehandle = 0;
        } else {
          this.$refs.sharpen.checked = false;
          this.$message.error("");
        }
      } else {
        if (this.$refs.histogram.checked === true) {
          this.$refs.histogram.checked = false;
        }

        if (this.$refs.sharpen.checked === false) {
          this.myhistogram.prehandle = 0;
          this.$message.success("");
          this.upload.prehandle = 0;
        } else {
          this.$message.success("");
          this.upload.prehandle = 4;
          this.mysharpen.prehandle = 4;
          let formData1 = new FormData();
          let formData2 = new FormData();

          for (const item of this.fileList1) {
            formData1.append("files", item) ||
              formData1.append("files", item.raw);
            formData1.append("type", "");
          }

          for (const item of this.fileList2) {
            formData2.append("files", item) ||
              formData2.append("files", item.raw);
            formData2.append("type", "");
          }
          let upload1 = new Promise((resolve, reject) => {
            this.createSrc(formData1).then((res) => {
              this.sharpenSrc1 = res.data.data.splice(0, 3);
              this.Img1 = this.sharpenSrc1.map((item) => {
                return global.BASEURL + item.src;
              });
              resolve();
            }).catch((rej) => { })
          });
          let upload2 = new Promise((resolve, reject) => {
            this.createSrc(formData2).then((res) => {
              this.sharpenSrc2 = res.data.data.splice(0, 3);
              this.Img3 = this.sharpenSrc2.map((item) => {
                return global.BASEURL + item.src;
              });
              resolve();
            }).catch((rej) => { })
          });
          Promise.all([upload1, upload2]).then((val) => {
            this.sharpenSrc = this.sharpenSrc1.concat(this.sharpenSrc2);
            this.sharpenSrc = this.sharpenSrc.map((item) => {
              return {
                filename: item.filename.substring(
                  item.filename.indexOf("/") + 1,
                  item.length
                ),
                src: item.src,
              };
            });
            this.shaPairs = this.sharpenSrc.map((item) => {
              return item.filename.substring(
                item.filename.indexOf("/") + 1,
                item.length
              );
            });

            this.checkPairs(this.hisPairs);

            if (!this.canUpload) {
              this.$message.error(
                ""
              );
              this.Img1 = [];
              this.Img3 = [];
            } else {
              this.mysharpen.list = this.getList(this.sharpenSrc);
              this.histogramUpload(this.mysharpen).then((res) => {
                this.sharpenImg1 = res.data.data.map((item) => {
                  return global.BASEURL + item.first;
                });

                this.sharpenImg2 = res.data.data.map((item) => {
                  return global.BASEURL + item.second;
                });
              });
            }
          }).catch((rej) => { })
        }
      }
    },

    selectFilter() {
      if (this.$refs.smooth.checked === true) {
        this.$refs.smooth.checked = false;
      }
      if (this.$refs.filter.checked === false) {
        this.$message.success("");
        this.upload.denoise = 0;
      } else {
        this.$message.success("");
        this.upload.denoise = 5;
      }
    },
    selectSmooth() {
      if (this.$refs.filter.checked === true) {
        this.$refs.filter.checked = false;
      }
      if (this.$refs.smooth.checked === false) {
        this.$message.success("");
        this.upload.denoise = 0;
      } else {
        this.$message.success("");
        this.upload.denoise = 3;
      }
    },
    uploadFirst() {
      this.checkFile1(...this.$refs.refFileA.files);
      if (this.canUpload) {
        this.fileList1.push(...this.$refs.refFileA.files);
      } else {
        setTimeout(() => {
          this.$message.error("");
        }, 1000);
      }
    },
    uploadSecond() {
      this.checkFile2(...this.$refs.refFileB.files);
      if (this.canUpload) {
        this.fileList2.push(...this.$refs.refFileB.files);
      } else {
        setTimeout(() => {
          this.$message.error("");
        }, 1000);
      }
    },
    file1Click() {
      document.querySelector("#upload-fileA").click();
    },
    file2Click() {
      document.querySelector("#upload-fileB").click();
    },

    checkPairs(list) {
      let s = list.join(",") + ",";
      let j = 0;
      for (let i = 0; i < list.length; i++) {
        if (s.replace(list[i] + ",", "").indexOf(list[i] + ",") > -1) {
        } else {
          j++;
          break;
        }
      }
      this.canUpload = j === 0;
    },
    changePreMode() {
      if (this.preMode === 1) {
        this.preMode = 2;
      } else {
        this.preMode = 1;
      }
    },
    sliderMouseMove(event) {
      const slider = document.querySelector("#image-slider");
      const wrapper = document.querySelector(".img-wrapper");
      const handle = document.querySelector(".handle");

      if (this.isSliderLocked) return;

      const sliderLeftX = slider.offsetLeft;

      const sliderWidth = slider.clientWidth;

      const sliderHandleWidth = handle.clientWidth;

      let mouseX = event.clientX - sliderLeftX;

      if (mouseX < 0) mouseX = 0;
      else if (mouseX > sliderWidth) mouseX = sliderWidth;

      wrapper.style.width = `${((1 - mouseX / sliderWidth) * 100).toFixed(4)}%`;

      handle.style.left = `calc(${((mouseX / sliderWidth) * 100).toFixed(
        4
      )}% - ${sliderHandleWidth / 2}px)`;

    },
    sliderMouseDown() {
      if (this.isSliderLocked) this.isSliderLocked = false;
      this.sliderMouseMove(event);
    },
    sliderMouseUp() {
      if (!this.isSliderLocked) this.isSliderLocked = true;
    },
    sliderMouseLeave() {
      if (this.isSliderLocked) this.isSliderLocked = true;
    },
    vanishDrag() {
      this.dragShow = false
    },
    hideMask() {
      this.isHiddenMask = !this.isHiddenMask
    }
  },
};

</script>
